import * as React from 'react'
import { ReactNode } from 'react'

import AppBar from '@material-ui/core/AppBar/AppBar'
import { Grid, Hidden, Tab, Tabs, Theme } from '@material-ui/core'
import CssBaseline from '@material-ui/core/CssBaseline/CssBaseline'
import { makeStyles, ThemeProvider } from '@material-ui/styles'
import HomeIcon from '@material-ui/icons/Home'
import logo from '../../page/components/logo.svg'
import { LinkTo } from '../../util'
import { TabIndex } from '../../page/components/shared'

const useStyles = makeStyles((theme: Theme) => ({
    tabs: {
        paddingLeft: '10vw'
    },
    content: {
        display: 'block', // IE11
        minHeight: '100vh',
        paddingTop: 64 + theme.spacing.unit + 'px', // account for toolbar height
        paddingBottom: '3vw',
        marginLeft: '10vw',
        marginRight: '10vw',
        position: 'relative'
    },
    logo: {
        marginTop: '20px',
        width: '150px',
        height: '150px'
    }
}))

export type Props = Readonly<{
    theme: Theme
}> &
    ThemedAppProps

const MainPage = ({ theme, tabIndex, setTabIndex, children }: Props) => {
    return (
        // This is the only place we need to access the theme via our own model
        // From here on, it is safe and convenient to use the `useTheme` hook
        <ThemeProvider theme={theme}>
            <ThemedApp tabIndex={tabIndex} setTabIndex={setTabIndex} children={children} />
        </ThemeProvider>
    )
}

type ThemedAppProps = Readonly<{
    tabIndex: TabIndex
    setTabIndex: (tabIndex: TabIndex) => void
    children?: ReactNode
}>

const ThemedApp = ({ tabIndex, setTabIndex, children }: ThemedAppProps) => {
    const classes = useStyles()

    const onTabChange = (event: React.ChangeEvent<{}>, v: any) => {
        setTabIndex(v)
    }

    return (
        <div>
            <CssBaseline />
            <AppBar>
                <Tabs value={tabIndex} onChange={onTabChange} indicatorColor="secondary" className={classes.tabs}>
                    <Tab icon={<HomeIcon />} component={LinkTo('/')} />
                    <Tab label="You" component={LinkTo('/you')} />
                    <Tab label="Me" component={LinkTo('/me')} />
                    <Tab label="How" component={LinkTo('/how')} />
                </Tabs>
            </AppBar>
            <div className={classes.content}>
                {children}
                <Hidden only={['xs', 'md', 'lg']}>
                    <Grid container={true} justify="flex-end">
                        <img src={logo} className={classes.logo} alt="Code Counseling" />
                    </Grid>
                </Hidden>
                <Hidden smUp={true}>
                    <Grid container={true} justify="center">
                        <img src={logo} className={classes.logo} alt="Code Counseling" />
                    </Grid>
                </Hidden>
            </div>
        </div>
    )
}

export default MainPage
