import * as React from 'react'
import { Routes } from '../../page'
import MainPage from '../components/MainPage'
import { Theme } from '@material-ui/core'
import { useState } from 'react'
import { TabIndex } from '../../page/components/shared'

export type Props = Readonly<{
    theme: Theme
}>

const App = ({ theme }: Props) => {
    const [tabIndex, setTabIndex] = useState<TabIndex>(0)
    return (
        <MainPage theme={theme} tabIndex={tabIndex} setTabIndex={setTabIndex}>
            <Routes tabIndex={tabIndex} setTabIndex={setTabIndex} />
        </MainPage>
    )
}

export default App
