import * as React from 'react'
import { Redirect, Route, Switch } from 'react-router'
import HomePage from './HomePage'
import YouPage from './YouPage'
import MePage from './MePage'
import HowPage from './HowPage'
import CvPage from './CvPage'
import { TabIndex } from './shared'

type Props = Readonly<{ tabIndex: TabIndex; setTabIndex: (tabIndex: TabIndex) => void }>

const Routes = ({ tabIndex, setTabIndex }: Props) => {
    const render = (page: React.ReactNode, newTabIndex: TabIndex) => () => {
        if (tabIndex !== newTabIndex) {
            setTabIndex(newTabIndex)
        }
        return page
    }

    return (
        <Switch>
            <Route exact={true} path="/" render={render(<HomePage />, 0)} />
            <Route exact={true} path="/you" render={render(<YouPage />, 1)} />
            <Route exact={true} path="/me" render={render(<MePage />, 2)} />
            <Route exact={true} path="/me/cv" render={render(<CvPage />, 2)} />
            <Route exact={true} path="/how" render={render(<HowPage />, 3)} />
            // Fallback for all invalid paths
            <Redirect to="/" />
        </Switch>
    )
}

export default Routes
